<template>
    <div class="content">
        <Navbar/>
        <div class="cases">
            <transition name="fade" mode="out-in" appear>
                <Case v-for="(item, index) in this.fetchedCases" v-bind:key="item.id" v-if="current_case === item.id"
                      :heading="item.name"
                      :description="item.short.description"
                      :does="item.short.does"
                      :image="item.short.image"
                      :link="item.link"
                />
            </transition>
            <div class="cases-navigation" v-if="fetchedCases">
                <a :class="[isFirstCase() ? 'case-link case-prev': 'case-link case-prev disabled']"
                   @click="prevCase">назад</a>
                <a @click="nextCase"
                   :class="[isLastCase() ? 'case-link case-next': 'case-link case-next disabled']">дальше</a>
            </div>
        </div>
    </div>
</template>

<script>
    import Navbar from "@/components/Navbar.vue"
    import Case from "@/components/Case.vue"
    import "../assets/styles/components/CaseOverviewPage.scss";

    export default {
        name: "Cases",
        components: {
            Navbar,
            Case
        },
        async mounted() {
            this.fetchedCases = this.$store.state.cases();
        },
        data() {
            return {
                current_case: 0,
                fetchedCases: null
            }
        },
        methods: {
            nextCase() {
                if (!(this.current_case >= Object.keys(this.fetchedCases).length - 1)) {
                    this.current_case += 1;
                }
            },
            prevCase() {
                if (!(this.current_case <= 0)) {
                    this.current_case -= 1;
                }
            },
            isLastCase() {
                return !(this.current_case >= Object.keys(this.fetchedCases).length - 1);
            },
            isFirstCase() {
                return !(this.current_case <= 0);
            }
        }
    }
</script>

